import { Component, OnInit, Injectable } from '@angular/core';
import {Usuario} from '../usuario';
import { HttpClient } from '@angular/common/http';

@Component({
  selector: 'app-user-list',
  templateUrl: './user-list.component.html',
  styleUrls: ['./user-list.component.css']
})
@Injectable()
export class UserListComponent implements OnInit {
  users:Usuario[];
  apiURL: string = 'http:/localhost:3000/gatos';
  constructor(private httpClient: HttpClient) {
    this.users =[
      {nombre:"Graciela",apellido:"OtroTesteo",edad:30},
      {nombre:"Pipa",apellido:"Gatita",edad:6},
      {nombre:"Negro",apellido:"Gatito",edad:4}
    ]
   }

  ngOnInit() {
    try {
      this.httpClient
      .get('http://localhost:64291/api/usuarios/vendedores')
      .subscribe((usuarios: Usuario[]) => this.users = usuarios,
      (error: any) => 
        console.log("Error al consumir la api")
        );
    } catch(e) {
      if(e instanceof Error) {
        console.log("Instance of error"+e)
      } else {
        console.log("Other error"+e)
      }
    }
  }
}
